<template>
  {{{content}}}
</template>

<style lang="styl" scoped>
</style>

<script>
  import Vue from 'vue'

  export default{
    name: 'Drops',

    data () {
      return {
        content: '',
        imgRoot: 'https://superkieran.github.io/wooyun-img/'
      }
    },

    created () {
      this.getDrop()
    },

    watch: {
      '$route.params.title': function () {
        this.getDrop()
      },
      'content': function () {
        Vue.nextTick(() => {
          let imgs = document.getElementsByTagName('img')
          let len = imgs.length
          for (let i = 0; i < len; i++) {
            if (imgs[i].getAttribute('img-src')) {
              imgs[i].setAttribute('src', this.imgRoot + imgs[i].getAttribute('img-src'))
            }
          }
        })
      }
    },

    methods: {
      getDrop () {
        if (this.$route.params.title) {
          let _content = localStorage.getItem(this.$route.params.title)
          if (!_content) {
            this.$http.get(`static/drops/${this.$route.params.title}.html`)
              .then(res => {
                this.content = res.body
                localStorage.setItem(this.$route.params.title, res.body)
              })
          } else {
            this.content = _content
          }
        }
      }
    }
  }
</script>



